.container-header {
  width: 280px;
  justify-content: space-between;
  padding: 12px 0 0 20px;
  display: flex;
}

.site-logo {
  fill: var(--accent);
}

.header-svg-logo {
  width: 18px;
  height: 37px;
}

.header-logo {
  position: relative;
}

.logo-text {
  letter-spacing: -.1em;
  text-shadow: 1.2px 0 #f8d24e, 0 1.2px #f8d24e, -1.2px 0 #f8d24e, 0 -1.2px #f8d24e;
  color: var(--background);
  font-family: Seymour One;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  display: inline-block;
  position: absolute;
  bottom: 1px;
  left: 15px;
}

.burger-btn {
  background-color: var(--background);
  border: rgba(0, 0, 0, 0);
}

.icon-burger, .close-menu-icon {
  fill: var(--primary-white-color);
}

.mobile-menu {
  width: 100%;
  height: 100%;
  z-index: 6;
  opacity: 0;
  visibility: hidden;
  pointer-events: auto;
  background-color: var(--background);
  margin: auto;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: painted;
}

.menu-logo {
  fill: var(--accent);
  justify-content: space-between;
  margin-bottom: 51px;
  display: flex;
}

.close-button-menu {
  background-color: var(--background);
  border: rgba(0, 0, 0, 0);
  padding: 0;
  display: block;
}

.header-list > .nav-item + .nav-item {
