}

.header-list .link {
  color: var(--white-text-color);
  flex-direction: column;
  align-items: center;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.21;
  display: flex;
}

.header-btn {
  background-color: var(--accent);
  border: rgba(0, 0, 0, 0);
  align-items: center;
  margin: 110px auto 0;
  padding: 12px 35px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
  display: flex;
}

.header-decoration-btn {
  margin-left: 4px;
}

.header-btn-tablet {
  display: none;
}

.header-list {
  letter-spacing: 0;
}

.header-title {
  width: 257px;
  margin-left: auto;
  margin-right: auto;
}

.header-mobile-menu {
  padding: 12px 32px 64px;
}

